.main-question {
	
}

.main-question .content-with-background {
	padding-top: 450px;
}
.main-question .content-with-background label {
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 50px;
}
.main-question .content-with-background label span {
    position: absolute;
    top: 90px;
    text-align: center;
	left: -28px;
    width: 110px;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
}
.main-question .content-with-background label > input {
	visibility: hidden; 
	position: absolute;
}
.main-question .content-with-background label > input + img{
	opacity: 0;
}
.main-question .content-with-background label > input:checked + img{
	opacity: 1;
}


